<template>
    <main>
        <section class="products">
            <div id="display">
                <h3>{{ title }}</h3>
            </div>
            <div v-if="scrollCardData != ''" class="scroll-section">
                <div 
                    v-for="(data, index) in scrollCardData"
                    :key="index"
                    class="scroll-card"
                >
                    <div class="card-img">
                        <div v-if="data.img != ''">
                            <ImgCard :link="data.img" />
                        </div>
                    </div>
                    <div class="card-content">
                        <ul>
                            <li>Items: {{ data.title }}</li>
                            <li>Price: ${{ data.price }}</li>
                        </ul>
                        <router-link :to="{ name: 'Product', params: { product: product, id: data._id } }" class="btn btn-scroll-card">Explore</router-link>
                    </div>
                </div>
            </div>
            <div v-else-if="error != null">
                <div class="card p-0-5 alert-error">
                    <span>{{ error }}!!!</span>
                </div>
            </div>
            <div v-else>
                <img src="@/assets/imgs/preloader.gif" alt="preloader" class="preloader-img">
            </div>
            <br>
            <h1 class="exclusive-header">Exclusive Offers</h1>
            <div class="exclusive-section container grid">
                <article 
                    v-for="(data, index) in exclusiveOffersData"
                    :key="index"
                    :class="`exclusive-card spacing ${data.color}`"
                >
                    <div v-if="data.img != ''">
                        <ImgCard :link="data.img" class="card-img" />
                    </div>
                    <h3 class="card-title">{{ data.title }}</h3>
                    <p class="card-description">{{ data.description }}</p>
                    <a href="" class="btn">Buy now</a>
                </article>
            </div>
        </section>
    </main>
</template>

<script>
import ImgCard from '@/components/ImgCard'
import categoryService from '@/data/categoryService'
import { onMounted } from '@vue/runtime-core'
import { ref } from '@vue/reactivity'

export default {
    name: 'BodyContent',
    props: ['product'],
    components: {
        ImgCard
    },
    setup(props) {
        const product = props.product
        const productDB = ref(null)
        const title = ref(null)

        switch (props.product) {
            case 'children-wears':
                productDB.value = 'FootwearDB'
                title.value = 'Children Wears'
                break;
            case 'toys':
                productDB.value = 'ToysDB'
                title.value = 'Toys'
                break;
            case 'baby-beds':
                productDB.value = 'BabybedDB'
                title.value = 'Baby Beds'
                break;
            case 'accessories':
                productDB.value = 'AccessoryDB'
                title.value = 'Accessories'
                break;
            default :
                productDB.value = 'FootwearDB'
                title.value = 'Children Wears'
                break;
        }
        const getData = () => categoryService(productDB.value)
        const { getCategory, scrollCardData, exclusiveOffersData, error } = getData()
        
        onMounted(getCategory)



        return { product, title, scrollCardData, exclusiveOffersData, error }
    }
}
</script>

<style scoped>
.card-content ul li {
    font-size: 12px;
    font-family: Arial;
}

@media(max-width: 700px) {
    .card-content ul li {
        font-size: 15px;
        font-family: Arial;
    }
}
</style>