import { ref } from '@vue/reactivity'
import axios from 'axios'

export default function categoryService(productDB) {
    const url = `api`
    const scrollCardData = ref([])
    const productData = ref(null)
    const exclusiveOffersData = ref([])
    const error = ref(null)
    const success = ref(null)

    const getCategory = async () => {
        try {
            const res = await axios.get(`${url}/${productDB}`)
            scrollCardData.value =  await res.data
            exclusiveOffersData.value = await res.data.filter((data) => data.exclusive === true)
            
        } catch (err) {
            error.value = err.message
        }
    }

    const postCategory = async (formData) => {
        try {
            const res = await axios.post(`${url}/${productDB}`, formData)
            success.value = res.data.message
        } catch (err) {
            error.value = err.message
        }
    }

    const getProduct = async (id) => {
        try {
            const res = await axios.get(`http://localhost:5000/api/${productDB}/${id}`)
            productData.value =  await res.data
        } catch (err) {
            error.value = err.message
        }
    }


    return { getCategory, scrollCardData, exclusiveOffersData, error, success, postCategory, getProduct, productData }
}