<template>
<div>
    <Header />
    <Categories />
    <div v-if="successMessage != null">
        <div class="card p-0-5 alert-success">
            <span>{{ successMessage }}!!!</span>
        </div>
    </div>
    <div v-if="err != null">
        <div class="card p-0-5 alert-error">
            <span>{{ err }}!!!</span>
        </div>
    </div>
    <div class="form-template-1">
        <div class="form-header">
            <h2 class="text-center p-2">Upload New Product</h2>
        </div>
        <form @submit.prevent="formDatas" enctype="multipart/form-data">
            <div class="form-control">
                <label for="img-file">Upload Image</label>
                <input type="file" ref="file" required/>
            </div>
            <div class="grid">
                <div class="form-control">
                    <label for="img-category">Image Category</label>
                    <select v-model="category" required>
                        <option disabled hidden selected>Select category</option>
                        <option value="FootwearDB">Children Wears</option>
                        <option value="ToysDB">Toys</option>
                        <option value="BabybedDB">Baby Beds</option>
                        <option value="AccessoryDB">Accessories</option>
                    </select>
                </div>
                <div class="form-control">
                    <label for="img-price">Image Price</label>
                    <div class="logo-input">
                        <div class="logo">$</div>
                        <input type="number" placeholder="Image price..." v-model="price" required/>
                    </div>
                </div>
                <div class="form-control">
                    <label for="img-title">Image Title</label>
                    <input type="text" placeholder="Image title..." v-model="title" required/>
                </div>
                <div class="form-control">
                    <label for="img-color">Image Color</label>
                    <select v-model="color" required>
                        <option value="shoe-blue">Blue</option>
                        <option value="shoe-red">Red</option>
                        <option value="shoe-white">White</option>
                    </select>
                </div>
            </div>
            <div class="form-control">
                <label for="img-description">Image Description</label>
                <textarea rows="4" placeholder="Describe The Product..." v-model="description" required></textarea>
            </div>
            <div class="form-control py-2">
                <label for="img-exclusive">Exclusive Offer</label>
                <input type="checkbox" v-model="exclusive" />
            </div>
            <button type="submit" class="btn btn-outline">UPLOAD</button>
        </form>
    </div>
    <Footer />
</div>
</template>

<script>
import Header from '@/components/Header'
import Categories from '@/components/Categories'
import Footer from '@/components/Footer'
import { ref } from '@vue/reactivity'
import categoryService from '@/data/categoryService'

export default {
    name: 'Form',
    components: {
        Header,
        Categories,
        Footer
    },
    setup() {
        const file = ref(null)
        const category = ref('')
        const price = ref('')
        const title = ref('')
        const color = ref('')
        const description = ref('')
        const exclusive = ref(false)
        
        const err = ref(null)
        const successMessage = ref(null)

        const formDatas = () => {
            let formData = new FormData();
            formData.append('file', file.value.files[0])
            formData.append('category', category.value)
            formData.append('price', price.value)
            formData.append('title', title.value)
            formData.append('color', color.value)
            formData.append('description', description.value)
            formData.append('exclusive', exclusive.value)
            
            const { postCategory, error, success } = categoryService(category.value)
            postCategory(formData)

            file.value = null
            category.value = ''
            price.value = ''
            title.value = ''
            color.value = ''
            description.value = ''
            exclusive.value = false
            err.value = error
            successMessage.value = success
        }
        

        return { file, category, price, title, color, description, exclusive, formDatas, err, successMessage }
    }
}
</script>

<style scoped>
.form-template-1 {
    margin: auto;
    margin-bottom: 30px;
    padding-bottom: 20px;
    border-radius: 5px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
    max-width: 700px;
    background-color: #232d49;
    color: #fff;
    display: block;
}

.form-header {
    border-bottom: 2px solid #fff;
}

.form-template-1 form {
    padding: 10px 10px;
}

.form-control {
    width: 100%;
    padding: 5px;
    position: relative;
}

.form control label {
    margin: 2px;
    display: block;
}

.form-control input,
.form-control .logo-input,
.form-control select,
.form-control textarea {
    border: 1.5px solid #fff;
    border-radius: 3px;
    margin: 2px;
    padding: 8px 5px;
    width: 100%;
    background: #161c2f;
    color: #fff;
    font-size: 16px;
}

.form-control .logo-input {
    margin: 0;
    padding: 0;
    display: flex;
}

.form-control .logo-input .logo {
    border-right: 1px solid #fff;
    padding: 5px 8px;
    display: inline-block;
    font-weight: bold;
    background: #080e20;
}

.form-control .logo-input input {
    border: 0;
    margin: 0;
    display: inline-block;
}

.form-control input[type="checkbox"] {
    position: absolute;
    left: 0;
    height: 20px;
}

.form-template-1 .btn {
    margin: auto;
    padding: 8px 30px;
    display: block;
    background-color: #fff;
    color: #151c27;
    font-size: 16px;
    font-weight: bold;
}

@media(max-width: 700px) {
    .form-template-1 {
        border-radius: 0;
        margin-bottom: 0;
    }
}
</style>