<template>
    <!-- Categories -->
    <div class="categories my-2">
        <h2 class="md text-center">Categories</h2>
        <nav class="flex">
            <li 
                v-for="category in categories"
                :key="category.name"
            >
                <router-link :to="{ name: category.link, params: { product: category.product } }">{{ category.name }}</router-link>
            </li>
        </nav>
    </div>
</template>

<script>
import { ref } from "vue";

export default {
    name: 'Categories',
    setup() {
        const categories = ref([
            { link: 'ProductCW', name: 'Children Wears', product: 'children-wears' }, 
            { link: 'ProductT', name: 'Toys', product: 'toys' }, 
            { link: 'ProductBB', name: 'Baby Beds', product: 'baby-beds' }, 
            { link: 'ProductA', name: 'Accessories', product: 'accessories' }
        ])

        return { categories }
    }
}
</script>

