import { createRouter, createWebHistory } from 'vue-router'
import Home from '@/views/Home.vue'
import About from '@/views/About.vue'
import Form from '@/views/Form.vue'
import ProductCW from '@/views/ProductCW.vue'
import ProductT from '@/views/ProductT.vue'
import ProductBB from '@/views/ProductBB.vue'
import ProductA from '@/views/ProductA.vue'
import Product from '@/views/Product.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about-us',
    name: 'About',
    component: About
  },
  {
    path: '/post-form',
    name: 'Form',
    component: Form
  },
  {
    path: '/:product',
    name: 'ProductCW',
    component: ProductCW,
    props: true
  },
  {
    path: '/:product',
    name: 'ProductT',
    component: ProductT,
    props: true
  },
  {
    path: '/:product',
    name: 'ProductBB',
    component: ProductBB,
    props: true
  },
  {
    path: '/:product',
    name: 'ProductA',
    component: ProductA,
    props: true
  },
  {
    path: '/:product/:id',
    name: 'Product',
    component: Product,
    props: true
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
