<template>
<div>
<Header />
<Categories />
<section class="product-template">
    <div id="display">
        <h3>{{ title }}</h3>
    </div>
    <div class="container p-1">
        <div v-if="productData != null" class="product-card">
            <ImgCard :link="productData.img" />

            <div class="product-content">
                <h3>{{ productData.title }}</h3>
                <p>{{ productData.description }}</p>
                <div class="blog_base">
                    <p>{{ productData.price }}</p>
                </div>
            </div>
            
            <a href="http://wa.me/+13018524550" class="btn btn-fill">MESSAGE US</a>
        </div>
        <div v-else-if="error != null">
            <div class="card p-0-5 alert-error">
                <span>{{ error }}!!!</span>
            </div>
        </div>
        <div v-else>
            <img src="@/assets/imgs/preloader.gif" alt="preloader" class="preloader-img">
        </div>
    </div>
</section>
<Footer />
</div>
</template>

<script>
import Header from '@/components/Header'
import Categories from '@/components/Categories'
import Footer from '@/components/Footer'
import ImgCard from '@/components/ImgCard'
import { ref } from '@vue/reactivity'
import { useRoute } from 'vue-router';
import { onMounted } from '@vue/runtime-core'
import categoryService from '@/data/categoryService'

export default {
    name: 'Product',
    components: {
        Header,
        Categories,
        Footer,
        ImgCard
    },
    setup() {
        const product = useRoute().params.product
        const productID = useRoute().params.id
        const productDB = ref(null)
        const title = ref(null)
        
        switch (product) {
            case 'children-wears':
                productDB.value = 'FootwearDB'
                title.value = 'Children Wears'
                break;
            case 'toys':
                productDB.value = 'ToysDB'
                title.value = 'Toys'
                break;
            case 'baby-beds':
                productDB.value = 'BabybedDB'
                title.value = 'Baby Beds'
                break;
            case 'accessories':
                productDB.value = 'AccessoryDB'
                title.value = 'Accessories'
                break;
        }
        
        const getData = () => categoryService(productDB.value)
        const { getProduct, productData, error } = getData()
        onMounted(getProduct(productID))

        return { title, getProduct, productData, error }
    }
}
</script>

<style scoped>
.product-template {
    margin: auto;
    font-size: 14px;
    background-color: whitesmoke;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.product-card {
   background: whitesmoke;
}

.product-card img {
    max-width: 700px;
}

.btn-fill {
    padding: 5px 15px;
    margin: 2px;
    background-color: #2b89e2;
    border-radius: 5px;
    color: white;
    font-size: 13px;
}
</style>