<template>
    <img :src="require(`@/assets/imgs/${url}`)" alt="Product Pics" />
</template>

<script>
export default {
    name: 'ImgCard',
    props: {
        link: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            url: ''
        }
    },
    created() {
        this.getLink()
    },
    methods: {
        getLink() {
            if (this.link === '') {
                this.url = 'preloader.gif'
            } else {
                this.url = this.link
            }
        }
    }
}
</script>